@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://use.fontawesome.com/releases/v5.8.1/css/all.css);
body {
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-weight: normal;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #222;
}
.app__inner {
    border-radius: 4px;
    width: 50%;
    height: 350px;
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
}
.logo {
    background-color: #2b75b1;
    height: 100%;
    width: 30%;
    color:#fff ;
    font-size: 40px;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    text-transform: uppercase;
}

.logo__icons i {
    font-size: 25px;
    margin: 0 5px;
}
.main {
    width: 70%;
    padding: 15px 30px;
    border: 1px solid #2b75b1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}
.app__form {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.app__search {
    font-size: 20px;
    padding: 6px 6px;
    margin-right: 15px;
    font-family: 'Oswald', sans-serif;
    height: 40px;
    box-sizing: border-box;
    width: 250px;
    text-transform: capitalize;
    border:1px solid #2b75b1;

}

.app__submit {
    color: #fff;
    font-size: 20px;
    font-family: 'Oswald', sans-serif;
    background-color: #2b75b1;
    border: none;
    /* padding: 10px; */
    width: 140px;
    outline: none;
    height: 40px;
    box-sizing: border-box;
    cursor: pointer;
}

.app__submit-inactive {
    background-color: #777;
}

.app__submit:not(.app__submit-inactive):hover {
    background-color: #fff;
    color: #2b75b1;
    border: 1px solid #2b75b1;
}
.outcomes {
    font-size: 22px;
    margin-top: 30px;
}

.outcomes__title {
    text-align: center;
    position: relative;
    margin-bottom: 15px;
}

.outcomes__title:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
    background-color: #2b75b1;
    top: 50px;

}

.outcomes__title span {
    font-size: 30px;
    margin-left: 5px;
}

.outcomes__row {
    margin: 15px 0;
}

.outcomes__row:last-of-type {
    margin-bottom: 0;
}

.outcomes__row span {
    margin: 0 5px 0 7px;
}

.outcomes__icon {
    display: inline-block;
    text-align: center;
    width: 40px;
}

.outcomes .fa-thermometer-three-quarters {
    color: red;
}

.outcomes .fa-sun {
    color: orange;
}

.outcomes .fa-wind {
    color: blue;
}

.error {
    color: red;
}
